import React from "react";
import { useInView } from 'react-intersection-observer';
import { SectionTitleWithBR, RoundItem } from "../../components";
import { images } from "..//../constants";
import "./Effects.css";

const Effects = () => {
    const { ref, inView } = useInView({
        threshold: 0,
        triggerOnce: true,
    })

    return (
        <div className="effects">
            <SectionTitleWithBR startText={" "} boldText={" "} />
            <div ref={ref}
                className={`effects__items ${inView ? 'effects__items-active' : ''}`}>
                <RoundItem
                    className={`${inView ? 'effects__item' : ''}`}
                    image={images.themeEffects1}
                    alt={"healthy body"}
                    text={" ,  "}
                />
                <RoundItem
                    className={`${inView ? 'effects__item' : ''}`}
                    image={images.themeEffects2}
                    alt={"picture of massage"}
                    text={"   "}
                />
                <RoundItem
                    className={`${inView ? 'effects__item' : ''}`}
                    image={images.themeEffects3}
                    alt={"healthy body"}
                    text={"  ,  "}
                />
                <RoundItem
                    className={`${inView ? 'effects__item' : ''}`}
                    image={images.themeEffects4}
                    alt={"healthy body"}
                    text={"   "}
                />
            </div>
        </div>
    );
};

export default Effects;
