import React, { useState, useEffect } from "react";
import { useInView } from 'react-intersection-observer';
import { SectionTitle } from "../../components";
import { images } from "../../constants";
import "./AddProduct.css";

const AddProduct = ({ fetchedData }) => {
    const image = "grapeMain";
    const [content, setContent] = useState('');
    
    useEffect(() => {
        const fetchData = async () => {
            if (fetchedData && fetchedData.data && fetchedData.data.data.landing_product) {
                setContent(fetchedData.data.data.landing_product.active_ingredient || '');
            }
        };
    
        fetchData();
    }, [fetchedData]);
    

    const { ref, inView } = useInView({
        threshold: 0,
        triggerOnce: true,
    })

    return (
        <div className="add-product" id="add-product">
            <SectionTitle
                startText={" "}
                boldText={""}
            />
            <section className="add-product__content">
                <div className="content__img">
                    <img ref={ref}
                    className={`content__img-img ${inView ? 'rollIn' : ''}`}
                    src={images[image]} alt="grape oil"/>
                </div>
                
                <div className="content__text">
                    <h3 className="content__text-subtitle">
                         
                    </h3>
                    <div className="description" dangerouslySetInnerHTML={{ __html: content }} />
                    {/* <p>
                        <b>  </b>   
                         ,      
                         ,      
                           .  
                         ,    
                          ,       
                        ,    .
                    </p>
                    <p>
                        <b> </b>      
                        ,    ,  
                          .  
                             
                         ',      
                            . 
                             ,
                         .
                    </p>
                    <p>
                        <b>  </b>    
                        ,    . 
                        ,      - 
                          .   
                              
                           .  
                              
                           , 
                        .   ,    
                        .
                    </p> */}
                </div>
            </section>
        </div>
    );
};

export default AddProduct;
